body {
  margin: 0;
  background-color: #020202;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.rgbline {
  position: relative;
  width: 100%;
  height: 7px;
  left: 0px;
  top: 0px;
  background: radial-gradient(50% 50% at 50% 50%, #FF0000 0%, #9206FF 57.81%, #5F00BD 100%);
}

.row_container {
  position: absolute;
  left: 5%;
  top: 10%;
  right: 5%;
}


.hello {
  width: 267px;
  height: 0px;
  font-family: 'Arial';
  font-style: normal;
  font-size: 44px;
  color: #FFFFFF;
}
a{
  margin-right: 10px;
  text-decoration: none;
}
.boxIcons{
  position: relative;
  align-items: center;
  align-content: center;

}
.name {
  left: 75px;
  font-family: 'Arial';
  font-style: normal;
  font-size: 54px;
  background: radial-gradient(50% 50% at 50% 50%, #FF0000 0%, #9206FF 57.81%, #5F00BD 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.description {
  width: 443px;
  left: 79px;
  font-family: 'Arial';
  font-style: bold;
  font-weight: 150;
  font-size: 17px;
  color: #CFCFCF;
}

.spandesk {
  color: #8c33ff;
}


.separador {
  width: 98%;
  height: 5px;
  background: #1B1B1B;
  border-radius: 10px;
  margin-bottom: 40px;
}

.title {
  font-family: 'Arial';
  font-weight: 500;
  font-size: 22px;
  color: #FFFFFF;
}

.desPost {
  font-family: 'Arial';
  font-weight: 500;
  font-size: 13px;
  color: #BCBCBC;
  width: 399px;
  height: 50;
}

.linguagem {
  position: absolute;
  font-family: 'Arial';
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  color: #686868;
  left: 10px;
}


.post {
  position: relative;
  left: 100px;
  width: 820px;
  height: 152px;
  background: #0B0B0B;
  border-radius: 1px;
  top: 55px;
  margin-bottom: 10px;
}

.row_container {
  display: flex;
  height: 100px;
}


.box {
  position: relative;
  top: 15px;
  left: 10px;
  height: 120px;
}
.scroll{
  overflow: scroll;
  overflow-y: auto;  
  -ms-overflow-style: none;
  scrollbar-width: none;
  height: 700px;
}

.scroll::-webkit-scrollbar {
  display: none;
}
img {
  border-radius: 50%;
  width: 130px;
  height: 113px;
}

@media (max-width: 820px) {
  .row_container {
    display: grid;
    /*grid-template-rows: 410px;*/
  }

  .description {
    width: 90%;
    left: 79px;
    font-family: 'Arial';
    font-style: bold;
    font-weight: 150;
    font-size: 17px;
    color: #CFCFCF;
  }


  .separador {
    width: 98%;
    height: 3px;
    background: #1B1B1B;
    border-radius: 10px;
    margin-bottom: 40px;
  }
  img {
    border-radius: 50%;
    width: 100px;
    height: 95px;
  }

  .post {
    position: relative;
    width: 99%;
    height: 100;
    left: 0;
    background: #0B0B0B;
    border-radius: 1px;
  }

  .col_2 {
    position: relative;
    align-items: center;
  }

  body {
    align-items: center;
  }

  .title {
    font-family: 'Arial';
    font-weight: 500;
    font-size: 20px;
    color: #FFFFFF;
  }
  
  .desPost {
    font-family: 'Arial';
    font-weight: 500;
    font-size: 13px;
    color: #BCBCBC;
    width: 329px;
    height: 50;
  }
  
  .linguagem {
    position: absolute;
    font-family: 'Arial';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    color: #686868;
    left: 10px;
  }
  .scroll{
    height: 300px;
  }
  
}